.header {
  /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, #449EEC 0%, #26D6E5 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: -webkit-linear-gradient(315deg, #449EEC 0%, #26D6E5 100%);
  background: -o-linear-gradient(315deg, #449EEC 0%, #26D6E5 100%);
  background: linear-gradient(135deg, #449EEC 0%, #26D6E5 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  height: 100vh;
  position: relative; }
  @media screen and (max-width: 767px) {
    .header {
      height: auto; } }
  .header canvas {
    position: absolute;
    bottom: 0; }
  .header .head {
    width: 80%;
    margin: 0 auto;
    height: 150px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center; }
  .header .header-content {
    width: 80%;
    margin: 0 auto; }
  .header img.logo {
    /*position: absolute;
      top:38%;
      left:50%;
      transform:translateX(-50%);*/
    width: 220px; }
  .header img.mini-logo {
    display: inline-block;
    width: 60px;
    height: auto; }
  .header img.menu_btn {
    display: inline-block;
    width: 25px;
    cursor: pointer; }
  .header img.scroll {
    position: absolute;
    bottom: 30px;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
