  .header .socials {
    position: absolute;
    top: 51px;
    right: 200px;
    display: none; }
    .header .socials .social-single {
      margin-left: 20px; }
  .header .langs {
    display: inline-block; }
    .header .langs p.langs__ {
      float: left;
      margin-right: 10px;
      opacity: 0.5; }
      .header .langs p.langs__.active {
        opacity: 1; }
  .header h2 {
    color: #ffffff;
    font-family: 'Martel Sans', sans-serif;
    font-size: 30px;
    font-weight: 400;
    line-height: 36px;
    margin-bottom: 40px; }
    @media (max-width: 600px) {
      .header h2 {
        font-size: 22px; } }
  .header p {
    color: #ffffff;
    font-family: 'Lato', sans-serif;
    font-size: 18px;
    font-weight: 400;
    line-height: 27px; }
    .header p a {
      color: #003c4b;
      font-family: 'Lato', sans-serif;
      font-size: 18px;
      font-weight: 400;
      line-height: 27px;
      text-decoration: none; }
    @media (max-width: 600px) {
      .header p {
        font-size: 16px; } }



